@import url(https://fonts.googleapis.com/css2?&family=Luckiest+Guy&display=swap);
.App {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("https://i.postimg.cc/GtZv1p9M/background.png");
  background-repeat: no-repeat;
  background-size: cover;
}

div#default {
  min-width: 1200px;
  min-height: 600px;
  display: flex;
  grid-gap: "10px";
  gap: "10px";
}

nav {
  width: 100%;
  z-index: 100;
}

.profileContainer {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.form-input-div {
  display: flex;
}

.field:hover {
    -webkit-filter: brightness(120%);
            filter: brightness(120%);
}

header {
    position: absolute;
    flex-direction: column;
    display: flex;
    justify-content: center;
    left: 0;
    top: 0;
    padding: 24px;
    width: 100vw;
    margin-top: 15vh;
}
header > h2 {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    justify-self: center;
    font-family: 'Luckiest Guy', cursive;
    font-size: 120px;
    margin: 0 auto;
    color: whitesmoke;
}

.subtitle {
    display: flex;
    flex-direction: column;
    margin: 30px auto;
    /* /background-color: #1115; */
    padding: 20px;
    border-radius: 10px;
    color: #ecdc10;
}

.subtitle > h3 {
    font-weight: 600;
    font-size: 32px;
}

.subtitle > h4 {
    font-weight: 400;
    font-size: 24px;
}

.startBtn {
    padding-block: 16px;
    padding-inline: 52px;
    background-color: #cc1020;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
    color: white;
    text-decoration: none;
    font-weight: 400;
    border-radius: 5px;
    margin: 20px auto;
}
.startBtn:hover {
    color: white;
    background-color: #ec2030;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: montserrat, sans-serif;
}
input,
button {
  -webkit-appearance: none;
          appearance: none;
  background: none;
  border: none;
  outline: none;
}

.UserManagement {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.page-not-found {
  text-align: center;
}

form {
  display: block;
  position: relative;
}

form:after {
  content: "";
  display: block;
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  z-index: 1;
  /*background-image: linear-gradient(to bottom right, #55d3fa, #fe4880);*/
}

form .form-inner {
  position: relative;
  display: block;
  padding: 30px;
  z-index: 2;
}

form .form-inner h2 {
  color: #2dc2f0;
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 30px;
}

form .form-inner .form-group {
  display: block;
  color: #666;
  width: 340px;
  margin-bottom: 15px;
  text-align: left;
}

.form-inner .form-group label {
  color: #666;
  font-size: 12px;
}

form .form-inner .form-group .form-text {
  display: block;
  width: 100%;
  margin-top: 5px;
}

form .form-inner .form-group input {
  display: block;
  width: 100%;
  padding: 10px 15px;
  border-radius: 8px;
  border-color: #666;
}

form .form-inner .form-group input:focus {
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
}

form .form-inner input[type="submit"],
input[type="register"],
input[type="loginView"],
input[type="teacherRegisterView"] {
  border-color: #666;
  display: inline-block;
  padding: 15px 20px;
  border-radius: 8px;
  background-image: linear-gradient(to bottom right, #55d3fa, #fe4880);
  font-weight: 450;
  cursor: pointer;
  width: 180px;
  text-align: center;
}

form .form-inner input[type="submit"]:hover,
.welcome button:hover {
  background-position: 100% 0%;
}

.welcome h2 {
  color: gray;
  font-size: 28px;
  font-weight: 500;
}

.welcome span {
  color: #fe4880;
  font-weight: 700;
}
